@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap&subset=vietnamese);
#root {
  height: 100%;
}

html,
body {
  height: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

@font-face {
  font-family: "SVN";
  src: url(/static/media/SVN-AGENCY-FB.462e12e2.ttf) format("truetype");
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Noto Sans', sans-serif;
  font-size: 14px;
}

*,
*:before,
*:after,
*:placeholder-shown {
  font-family: inherit;
}

